

@include tablet {
    .seccion-inferior {
        display: grid;
        grid-template-columns: 2fr 1fr ;
        column-gap: 2rem;
    }
}

.entrada-blog {
    margin-bottom: 2rem; // separa cada una de las entradas

    @include tablet {
        display: grid;
        grid-template-columns: 1fr 2fr;
        column-gap: 2rem;
    }
    // sirve para no tener margen en la parte inferior
    &:last-of-type {
        margin-bottom: 0; 
    }
}

.informacion-meta{
       span {
        color: $amarillo;
     }


}
.texto-entrada{
     a {
        color: $negro; 
        margin-top: 2rem;
        display: block;
        @include tablet {
            margin-top: 0;
        }
     }
     h4 {
        margin: 0;
        line-height: 1.4 ;

        // funcion que pinta de color verde
        &::after {
            content: '';
            display: block;
            width: 15rem;
            height: .5rem;
            background-color: $verde;
            margin-top: 1rem;
        }
     }

}